<template>
  <div class="cms-list-layout">
    <cms-prompt>
      <li>显示所有的平台活动信息</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>活动名称</label>
        <el-input v-model="queryParams.name" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>活动时间</label>
        <cms-picker v-model="queryParams.timer" @change="setTimer" ></cms-picker>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="promote.name" label="活动名称" min-width="220"></el-table-column>
       <el-table-column prop="promote.discountAmount" label="报名时间" min-width="220">
        <div slot-scope="scope">
        {{$moment(scope.row.promote.rule.signBeginTime,'YYYY-MM-DD')}} 至 {{$moment(scope.row.promote.rule.signEndTime,'YYYY-MM-DD')}}
        </div>
      </el-table-column>
      <el-table-column prop="discountAmount" label="活动时间" min-width="220">
        <div slot-scope="scope">
        {{$moment(scope.row.promote.promoteBeginTime,'YYYY-MM-DD')}} 至 {{$moment(scope.row.promote.promoteEndTime,'YYYY-MM-DD')}}
        </div>
      </el-table-column>
      <el-table-column label="是否已报名" min-width="120">
        <div slot-scope="scope">
           <span class="success" v-if="scope.row.hasSign">已报名</span>
           <span v-else>未报名</span>
          <!-- <span class="link">查看</span> -->
        </div>
      </el-table-column>
      <el-table-column label="报名状态"  min-width="160">
        <div slot-scope="scope">
           <span class="success" v-if="scope.row.signStatus==-1">未申请</span>
           <span class="success" v-if="scope.row.signStatus==0">报名待审核</span>
           <span class="success" v-if="scope.row.signStatus==1">已审核待支付</span>
           <span class="success" v-if="scope.row.signStatus==2">已支付报名成功</span>
           <span class="success" v-if="scope.row.signStatus==3">失败</span>
           <span class="success" v-if="scope.row.signStatus==4">撤回</span>
          <!-- <span class="link">查看</span> -->
        </div>
      </el-table-column>
      <el-table-column label="操作选项" width='380' fixed="right" align="center">
        <div slot-scope="scope">
          <el-button type="text" @click="$routerLink('/platformActivity/edit','view',{id:scope.row.promote.id})">查看活动详情</el-button>
          <el-button type="text"  @click="$routerLink('/platformActivity/edit','save',{id:scope.row.promote.id})">报名</el-button>
           <el-button type="text" @click="setPayOrder(scope.row.promote.id)">支付</el-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
    <!-- 选择商品组件 -->
   <pay-box  ref="pay" :orderNum="orderNum" payScene="storeApply"></pay-box> 
  </div>
</template>
<script>
import { activityPlatformPage, activityPlatformPrepay } from './../../../api/marketing/index.js'
import payBox from './../../../components/operation/pay'
import list from '@/mixins/list'
var defaultData = {
  name: '',
  beginTime: '',
  endTime: '',
  promoteType: '',
  size: '15',
  page: '1'
}
export default {
  mixins: [list],
  components: { payBox },
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      orderNum: ''
    }
  },
  methods: {
    setTimer(value) {
      this.queryParams.beginTime = value[0]
      this.queryParams.endTime = value[1]
      this.getTableData(activityPlatformPage, this.queryParams)
    },
    setPayOrder(promoteId) { // 订单预生产
      activityPlatformPrepay({ promoteId: promoteId }).then(res => {
        if (res.code === 200) {
          this.orderNum = res.data.orderNum
          this.$refs.pay.showPay()
        } else {
          this.$errorMessage(res.message)
        }
      })
    }
  },
  created() {
    this.getTableData(activityPlatformPage, this.queryParams)
  }
}
</script>

<style>
</style>



